#eachProject {
  width: 30%;
  padding: 3% 10% 0% 10%;
  float: left;
  text-align: justify;
  text-align-last: center;
  -moz-text-align-last: center;
}

#projectImage {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  opacity: 0.9;
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(95%);
  border-radius: 45px;
  -webkit-box-shadow: 5px 5px 10px grey;
          box-shadow: 5px 5px 10px grey;
}

#projectImage:hover {
  -webkit-transition: 2s ease;
  -o-transition: 2s ease;
  transition: 2s ease;
  -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
  filter: grayscale(0%);
  opacity: 1;
}

#seperateLink:link {
  text-decoration: none;
  color: #1f2421;
}

#seperateLink:visited {
  text-decoration: none;
  color: #1f2421;
}

#seperateLink:hover {
  color: red;
  -webkit-text-decoration: underline red;
          text-decoration: underline red;
}

#seperateLink:active {
  color: #1f2421;
  text-decoration: none;
}



